.testimonial {
  padding: 5rem 0;
}

.section-title {
  margin: 3rem 0;
  color: var(--dark-gray);
  text-align: center;
  text-transform: uppercase;
  letter-spacing: 0.4rem;
}

.cards {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  flex-wrap: wrap;
}

.card {
  border: 1px solid #ebebeb;
  width: 320px;
  height: 340px;
  padding: 1rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  text-align: center;
}

.card .img {
  border-radius: 50%;
  overflow: hidden;
  width: 100px;
  height: 100px;
}

.card img {
  object-fit: contain;
  width: 100%;
}

.card .comment {
  color: hsl(213, 9%, 39%);
  font-weight: 500;
}

.card .role {
  color: hsl(210, 4%, 67%);
  margin-top: 0.7rem;
}

.card .name {
  color: var(--very-dark-blue);
}
